.Payments form {
  align-self: center;
  box-shadow: 0px 0px 0px 0.5px var(--border-color),
    0px 2px 5px 0px rgba(50, 50, 93, 0.1), 0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
  border-radius: 0.4rem;
  padding: 1.5rem;
}
.Payments input {
  border-radius: 0.4rem;
  margin-bottom: 0.4rem;
  padding: 0.7rem;
  border: 0.1rem solid rgba(50, 50, 93, 0.1);
  max-height: 2.8rem;
  width: 100%;
  background: white;
}
.total {
  font-weight: 600;
}
/* .Payments .result-message {
    line-height: 22px;
  } */
.Payments .hidden {
  display: none;
}
.Payments #card-error {
  color: rgb(105, 115, 134);
  margin-top: 0.8rem;
  text-align: center;
}
.Payments #card-element {
  border-radius: 4px 4px 0 0;
  padding: 0.8rem;
  border: 1px solid rgba(50, 50, 93, 0.1);
  max-height: 2.8rem;
  width: 100%;
  background: white;
}
.Payments #payment-request-button {
  margin-bottom: 2rem;
}
/* Buttons and links */
.Payments button {
  background-color: var(--primary-color);
  /* font-family: Arial, sans-serif; */
  color: #ffffff;
  border-radius: 0 0 4px 4px;
  border: 0;
  padding: 0.8rem 1rem;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
  width: 100%;
}
